<template><section><h1>流动墙</h1>
<p><code v-pre>vc-trail-wall</code> 组件用于加载流动墙效果。实质是通过<code v-pre>vc-entity</code> 和 <code v-pre>vc-graphics-wall</code> 加载墙，并赋予自定义材质 <code v-pre>PolylineTrailMaterialProperty</code>实现。参考 <a href="https://www.wellyyss.cn/ysCesium/main/app.html">ysCesium|跃焱邵隼</a>。</p>
<h2>示例</h2>
<h3>加载流动墙效果</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-trail-wall :positions="positions" color="yellow" :minimumHeights="minimumHeights" imageUrl="./statics/SampleData/images/colors1.png" ref="wall" :interval="1000"></vc-trail-wall>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu mapStyle="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
