    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-trail-wall</span>
        <span class="hljs-attr">:positions</span>=<span class="hljs-string">"positions"</span>
        <span class="hljs-attr">color</span>=<span class="hljs-string">"yellow"</span>
        <span class="hljs-attr">:minimumHeights</span>=<span class="hljs-string">"minimumHeights"</span>
        <span class="hljs-attr">imageUrl</span>=<span class="hljs-string">"./statics/SampleData/images/colors1.png"</span>
        <span class="hljs-attr">ref</span>=<span class="hljs-string">"wall"</span>
        <span class="hljs-attr">:interval</span>=<span class="hljs-string">"1000"</span>
      &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-trail-wall</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-imagery-tianditu</span> <span class="hljs-attr">mapStyle</span>=<span class="hljs-string">"img_c"</span> <span class="hljs-attr">token</span>=<span class="hljs-string">"436ce7e50d27eede2f2929307e6b33c0"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-imagery-tianditu</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">const</span> lat = <span class="hljs-number">30.598026044</span>
  <span class="hljs-keyword">const</span> lng = <span class="hljs-number">114.302312702</span>
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">positions</span>: [
          { <span class="hljs-attr">lng</span>: lng - <span class="hljs-number">0.5</span>, <span class="hljs-attr">lat</span>: lat - <span class="hljs-number">0.5</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">50000</span> },
          { <span class="hljs-attr">lng</span>: lng - <span class="hljs-number">3</span>, <span class="hljs-attr">lat</span>: lat, <span class="hljs-attr">height</span>: <span class="hljs-number">0</span> },
          { <span class="hljs-attr">lng</span>: lng - <span class="hljs-number">3</span>, <span class="hljs-attr">lat</span>: lat - <span class="hljs-number">1</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">50000</span> },
          { <span class="hljs-attr">lng</span>: lng - <span class="hljs-number">1</span>, <span class="hljs-attr">lat</span>: lat - <span class="hljs-number">3</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">50000</span> },
          { <span class="hljs-attr">lng</span>: lng - <span class="hljs-number">0.5</span>, <span class="hljs-attr">lat</span>: lat - <span class="hljs-number">0.5</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">50000</span> }
        ],
        <span class="hljs-attr">minimumHeights</span>: [<span class="hljs-number">0</span>, <span class="hljs-number">0</span>, <span class="hljs-number">0</span>, <span class="hljs-number">0</span>, <span class="hljs-number">0</span>]
      }
    },
    mounted() {
      <span class="hljs-keyword">this</span>.$refs.wall.createPromise.then(<span class="hljs-function">(<span class="hljs-params">{ Cesium, viewer, cesiumObject }</span>) =&gt;</span> {
        viewer.zoomTo(viewer.entities)
      })
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">this</span>.cesiumInstance = cesiumInstance
        <span class="hljs-keyword">const</span> { Cesium, viewer } = <span class="hljs-keyword">this</span>.cesiumInstance
        viewer.scene.globe.depthTestAgainstTerrain = <span class="hljs-literal">true</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> 指定墙的位置数组。 <strong>结构：[{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定流动墙是否可见。</td>
</tr>
<tr>
<td>minimumHeights</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> 指定 wall 底部的高度数组。</td>
</tr>
<tr>
<td>maximumHeights</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> 指定 wall 顶部的高度数组。</td>
</tr>
<tr>
<td>outline</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定 wall 是否绘制轮廓线。</td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre>'yellow'</code></td>
<td><code v-pre>optional</code> 指定墙的颜色。</td>
</tr>
<tr>
<td>interval</td>
<td>Number</td>
<td><code v-pre>3000</code></td>
<td><code v-pre>optional</code> 指定墙材质流动一个周期的时间，单位毫秒。</td>
</tr>
<tr>
<td>imageUrl</td>
<td>String</td>
<td><code v-pre>''</code></td>
<td><code v-pre>optional</code> 指定墙的图片材质。</td>
</tr>
<tr>
<td>loop</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定墙的流动效果是否循环。</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例, 以及线实体实例。</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    const lat = 30.598026044
    const lng = 114.302312702
    export default {
      data() {
        return {
          positions: [
            { lng: lng - 0.5, lat: lat - 0.5, height: 50000 },
            { lng: lng - 3, lat: lat, height: 0 },
            { lng: lng - 3, lat: lat - 1, height: 50000 },
            { lng: lng - 1, lat: lat - 3, height: 50000 },
            { lng: lng - 0.5, lat: lat - 0.5, height: 50000 }
          ],
          minimumHeights: [0, 0, 0, 0, 0]
        }
      },
      mounted() {
        this.$refs.wall.createPromise.then(({ Cesium, viewer, cesiumObject }) => {
          viewer.zoomTo(viewer.entities)
        })
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          const { Cesium, viewer } = this.cesiumInstance
          viewer.scene.globe.depthTestAgainstTerrain = true
        }
      }
    }
  </script>